div[class^="circle"] {
  background: white;
  border-radius: 100%;
  display: inline-block;
  overflow: hidden;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    display: block;
    height: 100%;
    left: 0;
    background: #73cb3e;
  }
}
div.circle25:after {
  width: 25%;
}
div.circle50:after {
  width: 50%;
}
div.circle75:after {
  width: 75%;
}
div.circle100:after {
  width: 100%;
}
